export default {
  state: {
    show: false,
    type: 'done',
    text: undefined
  },
  mutations: {
    openSnackbar (state, payload) {
      state.type = payload.type
      state.text = payload.text
      state.show = true
    },
    closeSnackbar (state) {
      state.show = false
    }
  },
  actions: {},
  modules: {}
}
