<template>
  <div>
    <v-navigation-drawer
      app
      v-model="drawerOpen"
    >
      <v-list-item
        v-if="userMessage"
        two-line
        style="height: 64px"
      >
        <v-list-item-avatar rounded>
          <img
            v-if="userMessage.avatar"
            :src="userMessage.avatar"
            alt="user_avatar"
          >

          <v-icon
            v-else
            size="40"
          >
            mdi-account-circle
          </v-icon>
        </v-list-item-avatar>

        <v-list-item-content>
          <v-list-item-title>{{ userMessage.name }}</v-list-item-title>
          <v-list-item-subtitle v-if="userMessage.department && $store.state.department.dir && $store.state.department.dir[userMessage.department]">
            {{ $store.state.department.dir[userMessage.department].name }}
          </v-list-item-subtitle>
        </v-list-item-content>
      </v-list-item>

      <v-divider />

      <v-list>
        <v-list-item
          v-show="userMessage.authority.indexOf(aMenu.authority) !== -1 || !(typeof aMenu.authority === 'number')"
          v-for="(aMenu,index) in menuWithoutSubmenu"
          :key="'A' + index"
          :to="'/index' + aMenu.path"
          color="primary"
        >
          <v-list-item-icon>
            <v-icon>{{ aMenu.icon }}</v-icon>
          </v-list-item-icon>

          <v-list-item-title>{{ aMenu.name }}</v-list-item-title>
        </v-list-item>

        <v-list-group
          v-show="userMessage.authority.indexOf(aMenu.authority) !== -1 || !(typeof aMenu.authority === 'number')"
          v-for="(aMenu,index) in menuWithSubmenu"
          :key="'B' + index"
          :prepend-icon="aMenu.icon"
          color="rgba(0, 0, 0, 0.87)"
          :value="aMenu.path.indexOf($route.fullPath.split('/')[2]) !== -1"
        >
          <template v-slot:activator>
            <v-list-item-content>
              <v-list-item-title>{{ aMenu.name }}</v-list-item-title>
            </v-list-item-content>
          </template>

          <v-list-item
            link
            v-for="(item,childIndex) in aMenu.children"
            :key="'C' + childIndex"
            :to="'/index' + aMenu.path + item.path"
            color="primary"
          >
            <v-list-item-content>
              <v-list-item-title>{{ item.name }}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list-group>
      </v-list>
    </v-navigation-drawer>

    <AppBar @changePassword="resetPasswordDialog = true" />

    <v-main>
      <v-container fluid>
        <router-view :key="$route.fullPath" />
      </v-container>
    </v-main>

    <ResetPasswordDialog :show="resetPasswordDialog" />
  </div>
</template>

<script>
import AppBar from '@/components/index/AppBar'
import ResetPasswordDialog from '@/components/index/ResetPasswordDialog'

export default {
  components: {
    AppBar,
    ResetPasswordDialog
  },
  data () {
    return {
      menu: [],
      dialog: false,
      drawerOpen: true,
      userMessage: null,
      resetPasswordDialog: false,
      extend: true
    }
  },
  created () {
    this.checkPassword()
    this.getMessage()
    this.getMenu()
    this.getDepartment()
  },
  computed: {
    menuWithSubmenu () {
      return this.menu.filter((menu) => {
        return menu.children.length > 0
      })
    },
    menuWithoutSubmenu () {
      return this.menu.filter((menu) => {
        return menu.children.length === 0
      })
    }
  },
  methods: {
    getMessage () {
      this.axios({
        method: 'GET',
        url: '/administrator/message'
      }).then((response) => {
        if (response.data.status === 1) {
          this.userMessage = response.data.data.userMessage
        }
      })
    },
    checkPassword () {
      this.axios({
        method: 'GET',
        url: '/passport/check'
      }).then((response) => {
        if (response.data.status === 1) {
          if (response.data.data.result === false) {
            this.$store.commit('openSnackbar', {
              type: 'error',
              text: '为确保账户安全，请修改您的初始密码'
            })
            this.resetPasswordDialog = true
          }
        }
      })
    },
    getMenu () {
      this.axios({
        method: 'GET',
        url: '/message/menu'
      }).then((response) => {
        if (response.data.status === 1) {
          this.menu = response.data.data.menu
        }
      })
    },
    getDepartment () {
      this.axios({
        method: 'GET',
        url: '/organization/department',
        params: {
          query: {},
          options: {}
        }
      }).then((response) => {
        if (response.data.status === 1) {
          this.department = response.data.data.department
          const dir = {}
          response.data.data.department.forEach((item) => {
            dir[item._id] = item
          })
          this.$store.state.department.dir = dir
        }
      })
    }
  }
}
</script>

<style>
.v-icon {
  color: rgba(0, 0, 0, 0.54);
}

.v-card__subtitle {
  font-weight: bolder;
  padding-left: 0 !important;
}
</style>
