<template>
  <v-row>
    <v-col>
      <v-card max-width="300">
        <v-card-title>{{ fields.name }}字段管理</v-card-title>
        <v-data-table
          :headers="headers"
          :items="fieldValue"
          hide-default-footer
          items-per-page="100"
        >
          <template v-slot:item.fieldName="{ item }">
            {{ item.fieldValue }}
          </template>

          <template v-slot:item.actions="{ item }">
            <v-icon
              small
              class="mr-2"
              @click="operatingIndex = item"
            >
              mdi-pencil
            </v-icon>

            <v-icon
              small
              @click="operating = item;deleteDialog = true"
            >
              mdi-delete
            </v-icon>
          </template>
        </v-data-table>
        <v-card-actions>
          <v-spacer />

          <!--    添加字段dialog      -->
          <v-dialog
            max-width="300"
            v-model="addDialog.show"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                v-bind="attrs"
                v-on="on"
              >
                添加
              </v-btn>
            </template>

            <v-card>
              <v-card-title>添加字段</v-card-title>
              <v-card-text>
                <v-text-field
                  autofocus
                  v-model="addDialog.text"
                />
              </v-card-text>
              <v-card-actions>
                <v-spacer />
                <v-btn @click="addDialog.show = false">
                  取消
                </v-btn>
                <v-btn @click="addField">
                  确定
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-card-actions>
      </v-card>

      <!--   编辑字段dialog   -->
      <v-dialog
        max-width="300"
        v-model="editDialog"
      >
        <v-card>
          <v-card-title>编辑字段</v-card-title>
          <v-card-text>
            <v-text-field autofocus />
          </v-card-text>
          <v-card-actions>
            <v-spacer />
            <v-btn @click="editDialog = false">
              取消
            </v-btn>
            <v-btn @click="saveEdit">
              保存
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>

      <!--    删除字段dialog      -->
      <v-dialog
        max-width="300"
        v-model="deleteDialog"
      >
        <v-card>
          <v-card-title>删除这个字段</v-card-title>
          <v-card-text>确定要删除这个字段吗？</v-card-text>
          <v-card-actions>
            <v-spacer />
            <v-btn @click="deleteDialog = false">
              取消
            </v-btn>
            <v-btn @click="deleteItem">
              确定
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-col>
  </v-row>
</template>

<script>
export default {
  name: 'FieldEditing',
  props: {
    fields: {
      type: Object,
      default () {
        return []
      }
    }
  },
  data () {
    return {
      headers: [
        {
          text: '字段名',
          sortable: false,
          value: 'fieldName'
        },
        {
          text: '操作',
          value: 'actions',
          sortable: false
        }
      ],
      addDialog: {
        show: false,
        text: ''
      },
      editDialog: false,
      deleteDialog: false
    }
  },
  computed: {
    fieldValue () {
      const fieldValue = []
      this.$store.state.field.fields[this.fields.fieldName].forEach((item) => {
        fieldValue.push({
          fieldValue: item
        })
      })
      return fieldValue
    }
  },
  created () {
    this.getItems(this.fields.fieldName)
  },
  methods: {
    getItems (fieldName) {
      this.$store.commit('getItems', fieldName)
    },
    addField () {
      if (this.addDialog.text) {
        this.axios({
          method: 'POST',
          url: `/settings/field/${this.fields.fieldName}`,
          data: {
            fieldValue: this.addDialog.text
          }
        }).then((response) => {
          if (response.data.status === 1) {
            this.getItems(this.fields.fieldName)
          }
        })
      }
      this.addDialog.text = ''
      this.addDialog.show = false
    },
    deleteItem () {
      this.axios({
        method: 'DELETE',
        url: `/settings/field/${this.fields.fieldName}`,
        data: {
          fieldValue: this.operating.fieldValue
        }
      }).then((response) => {
        if (response.data.status === 1) {
          this.getItems(this.fields.fieldName)
          this.deleteDialog = false
        }
      })
    },
    saveEdit () {
      console.log(this.operatingIndex)
    }
  }
}
</script>

