import Vue from 'vue'
import Vuex from 'vuex'

import index from '@/data/index'
import passport from '@/data/passport'
import snackbar from '@/data/snackbar'
import processLinear from '@/data/progress-linear'
import i18n from '@/data/i18n'
import field from '@/data/field'
import operating from '@/data/operating'
import department from '@/data/department'

Vue.use(Vuex)

export default new Vuex.Store({
  modules: {
    snackbar,
    index,
    passport,
    processLinear,
    i18n,
    field,
    operating,
    department
  }
})
