<template>
  <v-container>
    <!--  部门树  -->
    <v-treeview
      activatable
      hoverable
      :items="items"
      item-key="_id"
      item-text="name"
      ref="tree"
    >
      <template
        slot="label"
        slot-scope="{ item }"
      >
        <div
          style="cursor: pointer"
          @click="handleClick(item)"
          @contextmenu="showMenu($event,item)"
        >
          <span class="mr-6">{{ item.name }}</span>
          <span v-if="item.supervisor">部门经理：{{ administratorDir[item.supervisor].name }}</span>
          <span v-else>没有设置部门经理</span>
        </div>
      </template>
    </v-treeview>

    <v-list>
      <v-subheader>部门成员</v-subheader>
      <v-list-item
        v-for="(member,index) in members"
        :key="index"
      >
        <v-list-item-avatar>
          <v-img
            v-if="member.avatar"
            :src="member.avatar"
          />

          <v-icon
            v-else
            size="40"
          >
            mdi-account-circle
          </v-icon>
        </v-list-item-avatar>
        <v-list-item-content>
          <v-list-item-title>{{ member.name }}</v-list-item-title>
        </v-list-item-content>
      </v-list-item>
    </v-list>

    <menu-component
      :menu="menu"
      @openMoveDialog="openMoveDialog"
      @openAuthorityDialog="openAuthorityDialog"
    />

    <add-dialog
      :add-dialog="addDialog"
      @addDepartment="addDepartment"
      :show.sync="addDialog.show"
    />

    <rename-dialog
      :rename-dialog="renameDialog"
      @renameDepartment="renameDepartment"
    />

    <move-dialog
      :move-dialog="moveDialog"
      @moveDepartment="moveDepartment"
    />

    <delete-dialog
      :delete-dialog="deleteDialog"
      :show.sync="deleteDialog.show"
      @deleteDepartment="deleteDepartment"
    />

    <!--  设置权限  -->
    <v-dialog
      persistent
      v-model="setAuthorityDialog"
      max-width="300"
    >
      <v-card>
        <v-card-title>设置权限</v-card-title>
        <v-card-text>
          <v-checkbox
            dense
            v-model="selectedAuthority"
            v-for="(item,index) in authority"
            :key="index"
            :label="item.name"
            :value="index"
          />
        </v-card-text>
        <v-card-actions>
          <v-spacer />
          <v-btn @click="setAuthorityDialog = false">
            取消
          </v-btn>
          <v-btn @click="setAuthority">
            确定
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!--  设置部门主管  -->
    <v-dialog
      v-model="setSupervisorDialog"
      max-width="300"
    >
      <v-card>
        <v-card-title>设置部门主管</v-card-title>
        <v-card-text>
          <v-radio-group v-model="supervisor">
            <v-radio
              v-for="n in departmentMembers"
              :key="n._id"
              :label="n.name"
              :value="n._id"
            />
          </v-radio-group>
        </v-card-text>
        <v-card-actions>
          <v-spacer />
          <v-btn @click="setSupervisorDialog = false">
            取消
          </v-btn>
          <v-btn @click="setSupervisor">
            确定
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
import menu from '@/components/department/menu'
import addDialog from '@/components/department/add-dialog'
import renameDialog from '@/components/department/rename-dialog'
import moveDialog from '@/components/department/move-dialog'
import deleteDialog from '@/components/department/delete-dialog'

export default {
  components: {
    'menu-component': menu,
    'add-dialog': addDialog,
    'rename-dialog': renameDialog,
    'move-dialog': moveDialog,
    'delete-dialog': deleteDialog
  },
  data: () => ({
    administrator: [],
    administratorDir: {},
    items: [],
    members: [],
    operating: undefined,
    menu: {
      show: false,
      x: 0,
      y: 0,
      id: undefined
    },
    addDialog: {
      name: '',
      show: false
    },
    renameDialog: {
      show: false,
      name: ''
    },
    moveDialog: {
      show: false,
      items: [],
      selected: undefined
    },
    deleteDialog: {
      show: false
    },
    setAuthorityDialog: false,
    authority: [],
    selectedAuthority: [],
    setSupervisorDialog: false,
    supervisor: ''
  }),
  created () {
    this.getAdministrator()
    this.getDepartment()
    this.getMenu()
  },
  computed: {
    departmentMembers () {
      const that = this
      if (this.operating) {
        return this.administrator.filter((value) => {
          return value.department === that.operating._id
        })
      } else {
        return []
      }
    }
  },
  methods: {
    // 获取管理员
    getAdministrator () {
      this.axios({
        method: 'GET',
        url: '/organization/administrator'
      }).then((response) => {
        if (response.data.status === 1) {
          this.administrator = response.data.data.administrator
          const administratorDir = {}
          this.administrator.forEach((item) => {
            administratorDir[item._id] = item
          })
          this.administratorDir = administratorDir
        }
      })
    },
    // 右键打开菜单
    showMenu (event, item) {
      event.preventDefault()
      this.operating = item
      this.selectedAuthority = JSON.parse(JSON.stringify(item.authority))
      this.menu.x = event.clientX
      this.menu.y = event.clientY
      this.menu.show = true
    },
    // 获取所有部门
    getDepartment () {
      this.axios({
        method: 'GET',
        url: '/organization/department',
        params: {
          query: {},
          options: {}
        }
      }).then((response) => {
        if (response.data.status === 1) {
          const departmentDir = {}
          response.data.data.department.forEach((item) => {
            departmentDir[item._id] = item
          })

          // 递归生成部门树
          ;(function recursion (department) {
            if (department.children && department.children.length !== 0) {
              department.children.forEach((item, index) => {
                department.children[index] = departmentDir[item]
                recursion(department.children[index])
              })
            }
          })(response.data.data.department[0])

          this.items = [response.data.data.department[0]]
          const that = this
          setTimeout(function () {
            that.$refs.tree.updateAll(true)
          }, 500)
        }
      })
    },
    handleClick (item) {
      this.axios({
        method: 'GET',
        url: '/administrator',
        params: {
          query: {
            department: item._id
          },
          options: {}
        }
      }).then((response) => {
        if (response.data.status === 1) {
          this.members = response.data.data.administrator
        }
      })
    },
    // 添加部门
    addDepartment () {
      if (this.addDialog.name.length !== 0 && this.operating) {
        this.axios({
          method: 'POST',
          url: '/organization/department',
          data: {
            operating: this.operating._id,
            name: this.addDialog.name
          }
        }).then((response) => {
          if (response.data.status === 1) {
            this.$store.commit('openSnackbar', {
              type: 'done',
              text: response.data.message
            })
            this.getDepartment()
          }
        })
      }
      this.addDialog.name = ''
      this.addDialog.show = false
    },
    // 重命名部门
    renameDepartment () {
      this.renameDialog.show = false
      if (this.renameDialog.name.length !== 0) {
        this.axios({
          method: 'PUT',
          url: '/organization/department/rename',
          data: {
            operating: this.operating,
            name: this.renameDialog.name
          }
        }).then((response) => {
          if (response.data.status === 1) {
            this.renameDialog.name = ''
            this.$store.commit('openSnackbar', {
              type: 'done',
              text: response.data.message
            })
            this.getDepartment()
          }
        })
      }
    },
    openMoveDialog () {
      this.axios({
        method: 'GET',
        url: '/organization/department',
        params: {
          query: {},
          options: {}
        }
      }).then((response) => {
        if (response.data.status === 1) {
          this.moveDialog.show = true
          this.moveDialog.items = response.data.data.department
        }
      })
    },
    // 移动部门
    moveDepartment () {
      this.moveDialog.show = false
      if (this.moveDialog.selected !== undefined) {
        this.axios({
          method: 'PUT',
          url: '/organization/department/move',
          data: {
            operating: this.operating,
            moveId: this.moveDialog.items[this.moveDialog.selected]._id
          }
        }).then((response) => {
          if (response.data.status === 1) {
            this.$store.commit('openSnackbar', {
              type: 'done',
              text: response.data.message
            })
            this.getDepartment()
          }
        })
      }
    },
    // 删除部门
    deleteDepartment () {
      this.axios({
        method: 'DELETE',
        url: '/organization/department',
        data: {
          operating: this.operating._id
        }
      }).then((response) => {
        if (response.data.status === 1) {
          this.getDepartment()
        }
      })
      this.deleteDialog.show = false
    },
    // 获取所有菜单
    getMenu () {
      this.axios({
        method: 'GET',
        url: '/message/menu'
      }).then((response) => {
        if (response.data.status === 1) {
          const authority = []
          response.data.data.menu.forEach((item) => {
            if (typeof item.authority === 'number') {
              authority.push(item)
            }
          })
          this.authority = authority
        }
      })
    },
    openAuthorityDialog () {
      this.setAuthorityDialog = true
    },
    setAuthority () {
      this.axios({
        method: 'PUT',
        url: '/organization/department/set-authority',
        data: {
          _id: this.operating,
          authority: this.selectedAuthority
        }
      }).then((response) => {
        if (response.data.status === 1) {
          this.getDepartment()
        }
        this.setAuthorityDialog = false
      })
    },
    setSupervisor () {
      this.axios({
        method: 'PUT',
        url: '/organization/department/supervisor',
        data: {
          _id: this.operating._id,
          supervisor: this.supervisor
        }
      }).then((response) => {
        if (response.data.status === 1) {
          this.getDepartment()
          this.setSupervisorDialog = false
        }
      })
    }
  }
}
</script>
