<template>
  <v-container>
    <v-data-table
      :headers="headers"
      item-key="_id"
      :items="administrator"
      :search="search"
    >
      <template v-slot:top>
        <v-row>
          <v-col md="4">
            <v-text-field
              dense
              class="mr-sm-5"
              v-model="search"
              flat
              outlined
              prepend-inner-icon="mdi-magnify"
              label="Search"
            />
          </v-col>

          <v-col md="4">
            <v-select
              dense
              :items="department"
              item-text="name"
              item-value="name"
              label="Department"
              outlined
              @change="selectDepartment"
              value="company"
            />
          </v-col>

          <v-spacer />

          <v-col>
            <v-dialog
              v-model="addAdministratorDialog"
              transition="dialog-bottom-transition"
              max-width="600"
            >
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  v-bind="attrs"
                  v-on="on"
                >
                  添加管理员
                </v-btn>
              </template>
              <template v-slot:default>
                <v-card>
                  <v-card-title>添加管理员</v-card-title>
                  <v-card-text>
                    <v-text-field
                      autofocus
                      label="管理员账号"
                      v-model="form.username"
                    />
                    <v-text-field
                      label="管理员姓名"
                      v-model="form.name"
                    />
                  </v-card-text>
                  <v-card-actions>
                    <v-spacer />
                    <v-btn @click="addAdministratorDialog = false">
                      取消
                    </v-btn>
                    <v-btn @click="addAdministrator">
                      确定
                    </v-btn>
                  </v-card-actions>
                </v-card>
              </template>
            </v-dialog>
          </v-col>
        </v-row>
      </template>

      <template v-slot:item.department="{ item }">
        <span v-if="departmentDir[item.department]">
          {{ departmentDir[item.department].name }}
        </span>
      </template>

      <template v-slot:item.actions="{ item }">
        <v-icon
          small
          class="mr-2"
          @click="operating = item._id;editDialog.show = true"
        >
          mdi-pencil
        </v-icon>
        <v-icon
          small
          @click="operating = item._id;deleteDialog.show = true"
        >
          mdi-delete
        </v-icon>
      </template>
    </v-data-table>

    <v-dialog
      v-model="editDialog.show"
      persistent
      max-width="300"
    >
      <v-card>
        <v-card-title>Edit information</v-card-title>
        <v-card-text>
          <v-select
            :items="department"
            item-text="name"
            item-value="_id"
            label="Department"
            outlined
            clearable
            v-model="editDialog.department"
          />
        </v-card-text>

        <v-card-actions>
          <v-spacer />
          <v-btn @click="editDialog.show = false">
            Cancel
          </v-btn>
          <v-btn @click="editAdministrator">
            Ok
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog
      v-model="deleteDialog.show"
      persistent
      max-width="300"
    >
      <v-card>
        <v-card-title>Delete Administrator</v-card-title>
        <v-card-text>Are you sure to delete this manager?</v-card-text>
        <v-card-actions>
          <v-spacer />
          <v-btn @click="deleteDialog.show = false">
            Cancel
          </v-btn>
          <v-btn @click="deleteAdministrator">
            OK
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
export default {
  data () {
    return {
      headers: [
        {
          text: '头像',
          value: 'avatar'
        },
        {
          text: '名字',
          value: 'name'
        },
        {
          text: '部门',
          value: 'department'
        },
        {
          text: '操作',
          value: 'actions',
          sortable: false
        }
      ],
      department: [],
      departmentDir: {},
      search: '',
      administrator: [],
      operating: undefined,
      addAdministratorDialog: false,
      editDialog: {
        show: false,
        department: undefined
      },
      deleteDialog: {
        show: false
      },
      form: {
        username: '',
        name: ''
      }
    }
  },
  created () {
    this.getAdministrator()
    this.getDepartment()
  },
  methods: {
    getAdministrator () {
      this.axios({
        method: 'GET',
        url: '/organization/administrator'
      }).then((response) => {
        if (response.data.status === 1) {
          this.administrator = response.data.data.administrator
        }
      })
    },
    getDepartment () {
      this.axios({
        method: 'GET',
        url: '/organization/department',
        params: {
          query: {},
          options: {}
        }
      }).then((response) => {
        if (response.data.status === 1) {
          this.department = response.data.data.department
          const dir = {}
          response.data.data.department.forEach((item) => {
            dir[item._id] = item
          })
          this.departmentDir = dir
        }
      })
    },
    selectDepartment (event) {
      this.search = event
    },
    editAdministrator () {
      this.axios({
        method: 'PUT',
        url: '/organization/administrator',
        data: {
          operating: this.operating,
          department: this.editDialog.department
        }
      }).then((response) => {
        if (response.data.status === 1) {
          this.getAdministrator()
        }
      })
      this.editDialog.show = false
      this.editDialog.department = undefined
    },
    deleteAdministrator () {
      this.deleteDialog.show = false
      this.axios({
        method: 'DELETE',
        url: '/organization/administrator',
        data: {
          operating: this.operating
        }
      }).then((response) => {
        if (response.data.status === 1) {
          this.getAdministrator()
        }
      })
    },
    addAdministrator () {
      if (this.form.username && this.form.name) {
        this.axios({
          method: 'POST',
          url: '/organization/administrator',
          data: {
            username: this.form.username,
            name: this.form.name
          }
        }).then((response) => {
          if (response.data.status === 1) {
            this.getAdministrator()
            this.addAdministratorDialog = false
            this.form = {
              username: '',
              name: ''
            }
          }
        })
      }
    }
  }
}
</script>

