<template>
  <v-row>
    <v-col
      cols="12"
      md="6"
    >
      <v-card>
        <v-card-title>拜访记录</v-card-title>
        <v-card-text>
          <v-data-table
            :headers="headers"
            :items="visit"
          >
            <template v-slot:item.date="{ item }">
              {{ moment(item.date).format('YYYY-MM-DD') }}
            </template>
          </v-data-table>
        </v-card-text>
      </v-card>
    </v-col>

    <v-col
      cols="12"
      md="6"
    >
      <v-card>
        <v-card-title>添加拜访记录</v-card-title>
        <v-card-text>
          <v-form>
            <v-menu
              v-model="datePickerMenu"
              :close-on-content-click="false"
              :nudge-right="40"
              transition="scale-transition"
              offset-y
              min-width="auto"
            >
              <template v-slot:activator="{ on, attrs }">
                <v-text-field
                  v-model="form.date"
                  label="点击选择拜访时间"
                  prepend-icon="mdi-calendar"
                  readonly
                  v-bind="attrs"
                  v-on="on"
                />
              </template>
              <v-date-picker
                v-model="form.date"
                @input="datePickerMenu = false"
              />
            </v-menu>

            <v-radio-group v-model="form.contact">
              <v-radio
                v-for="(contact,index) in operating.contacts"
                :key="index"
                :label="contact.name"
                :value="contact.name"
              />
            </v-radio-group>

            <v-textarea
              v-model="form.content"
              label="拜访记录"
              outlined
            />
          </v-form>
        </v-card-text>
        <v-card-actions>
          <v-spacer />
          <v-btn @click="reset">
            重填
          </v-btn>

          <v-btn @click="addVisit">
            添加
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
import moment from 'moment'

export default {
  name: 'Visit',
  data () {
    return {
      moment: moment,
      datePickerMenu: false,
      headers: [
        {
          text: '拜访日期',
          value: 'date'
        },
        {
          text: '联系人',
          value: 'contact'
        },
        {
          text: '拜访内容',
          value: 'content'
        }
      ],
      visit: [],
      form: {
        date: moment().format('YYYY-MM-DD'),
        contact: undefined,
        content: undefined
      },
      operating: this.$store.state.operating.operating
    }
  },
  created () {
    this.getVisit()
  },
  methods: {
    reset () {
      this.form = {
        date: undefined,
        contact: undefined,
        content: undefined
      }
    },
    getVisit () {
      this.axios({
        method: 'GET',
        url: '/crm/customer/visit',
        params: {
          operatingId: this.$store.state.operating.operating._id
        }
      }).then((response) => {
        if (response.data.status === 1) {
          this.visit = response.data.data.visit
        }
      })
    },
    addVisit () {
      // this.form.date = moment(this.form.date, 'YYYY-MM-DD')
      this.axios({
        method: 'POST',
        url: '/crm/customer/visit',
        data: {
          operatingId: this.$store.state.operating.operating._id,
          form: this.form
        }
      }).then((response) => {
        if (response.data.status === 1) {
          this.form = {
            date: undefined,
            contact: undefined,
            content: undefined
          }
          this.getVisit()
        }
      })
    }
  }
}
</script>

