<template>
  <v-app-bar app>
    <v-app-bar-nav-icon @click="$parent.drawerOpen = !$parent.drawerOpen" />

    <v-spacer />

    <v-app-bar-nav-icon>
      <v-icon>mdi-message</v-icon>
    </v-app-bar-nav-icon>

    <v-menu
      top
    >
      <template v-slot:activator="{ on, attrs }">
        <v-app-bar-nav-icon
          v-bind="attrs"
          v-on="on"
        >
          <v-icon>mdi-dots-vertical</v-icon>
        </v-app-bar-nav-icon>
      </template>

      <v-list>
        <v-list-item>
          <v-list-item-title>修改个人信息</v-list-item-title>
        </v-list-item>
        <v-list-item>
          <v-list-item-title @click="$emit('changePassword')">
            修改密码
          </v-list-item-title>
        </v-list-item>
        <v-list-item>
          <v-list-item-title @click="signOut">
            退出登录
          </v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>
  </v-app-bar>
</template>

<script>
export default {
  name: 'AppBar',
  methods: {
    signOut () {
      this.axios({
        method: 'DELETE',
        url: '/passport/authentication'
      }).then((response) => {
        if (response.data.status === 1) {
          // 删除localStorage里的token
          localStorage.removeItem('token')
          localStorage.removeItem('exp')

          this.$router.push('/passport')
        }
      })
    }
  }
}
</script>

