<template>
  <v-container>
    <v-row>
      <v-col
        cols="12"
        sm="5"
      >
        <v-breadcrumbs
          :items="breadcrumbs"
          divider="/"
        />
      </v-col>

      <v-spacer/>

      <v-col
        cols="12"
        sm="4"
        class="d-flex align-center"
      >
        <v-progress-linear
          height="15"
          value="30"
          top
        >
          <template>
            <strong>{{ usedSize }} used / 1.0GB total</strong>
          </template>
        </v-progress-linear>
      </v-col>
    </v-row>

    <v-row>
      <v-col>
        <v-btn class="operation-button" :disabled="relativePathArr.length === 0" @click="back">
          <v-icon>mdi-arrow-left</v-icon>
          <span>back</span>
        </v-btn>

        <v-btn
          class="operation-button"
          @click="uploadFile"
        >
          <v-icon>mdi-cloud-upload</v-icon>
          <span>upload files</span>
        </v-btn>

        <v-btn
          class="operation-button"
          @click="newFolder.show = true"
        >
          <v-icon>mdi-folder</v-icon>
          <span>new folder</span>
        </v-btn>

        <v-btn
          class="operation-button"
          @click="getFiles"
        >
          <v-icon>mdi-refresh</v-icon>
          <span>refresh</span>
        </v-btn>
      </v-col>
    </v-row>

    <ul class="d-flex flex-wrap">
      <li
        class="file d-flex flex-column mb-8 mr-8 mr-sm-10"
        v-for="(item,index) in fileList"
        :key="index"
        :title="item.name"
        @dblclick="handleDblclick(item)"
        @contextmenu="showMenu($event,item)"
      >
        <svg
          class="icon"
          aria-hidden="true"
        >
          <use :xlink:href="'#extension-' + item.extname"/>
        </svg>
        <span>{{ item.name }}</span>
      </li>
    </ul>

    <transfile-dialog
      :transfile="transfile"
      @cancelUpload="cancelUpload"
    ></transfile-dialog>

    <new-folder-dialog
      :newFolder="newFolder"
      @createNewFolder="createNewFolder"
    ></new-folder-dialog>

    <delete-dialog
      @removeFile="removeFile"
      :show="deleteDialog.show"
    ></delete-dialog>

    <rename-dialog
      @renameFile="renameFile"
      :renameDialog="renameDialog"
    ></rename-dialog>

    <app-menu
      :menu="menu"
      @download="download"
      @rename="renameDialog.show = true"
      @remove="deleteDialog.show = true"
    ></app-menu>
  </v-container>
</template>

<script>
import menu from '@/components/resource/menu'
import deleteDialog from '@/components/resource/delete-dialog'
import renameDialog from '@/components/resource/rename-dialog'
import transfileDialog from '@/components/resource/transfile-dialog'
import newFolderDialog from '@/components/resource/new-folder-dialog'
import '../../public/fileicon/iconfont'
import fileSize from 'filesize'

export default {
  components: {
    'app-menu': menu,
    'delete-dialog': deleteDialog,
    'rename-dialog': renameDialog,
    'transfile-dialog': transfileDialog,
    'new-folder-dialog': newFolderDialog
  },
  data () {
    return {
      type: undefined,
      relativePathArr: [],
      usedSize: 0,
      fileList: [],
      transfile: {
        show: false,
        uploadingFiles: [],
        uploadingFilesLen: 0
      },
      newFolder: {
        show: false,
        name: ''
      },
      operatingFile: undefined,
      menu: {
        show: false,
        x: 0,
        y: 0
      },
      deleteDialog: {
        show: false
      },
      renameDialog: {
        show: false,
        name: ''
      }
    }
  },
  computed: {
    breadcrumbs () {
      const arr = []
      this.relativePathArr.map((item) => {
        arr.push({ text: item })
      })

      return [
        {
          text: this.type,
          disabled: true
        }
      ].concat(arr)
    },
    relativePath () {
      return '/' + this.relativePathArr.join('/') + '/'
    }
  },
  mounted () {
    this.type = this.$route.fullPath.split('/')[3]
    this.getFiles()
  },
  methods: {
    getFiles () {
      this.axios({
        method: 'GET',
        url: '/resource/file-list',
        params: {
          type: this.type,
          relativePath: this.relativePath
        }
      }).then((response) => {
        if (response.data.status === 1) {
          this.fileList = response.data.data.fileList
          this.getStorageSize()
        }
      })
    },
    getStorageSize () {
      this.axios({
        method: 'GET',
        url: '/resource/storage-size',
        params: {
          type: this.type
        }
      }).then((response) => {
        this.usedSize = fileSize(response.data.data.usedSize)
      })
    },
    createNewFolder () {
      this.newFolder.show = false
      this.axios({
        method: 'POST',
        url: '/resource/folder',
        data: {
          type: this.type,
          relativePath: this.relativePath,
          name: this.newFolder.name
        }
      }).then((response) => {
        if (response.data.status === 1) {
          this.$store.commit('openSnackbar', {
            type: 'done',
            text: response.data.message
          })
          this.getFiles()
        }
        this.newFolder.name = ''
      })
    },
    uploadFile () {
      const input = document.createElement('input')
      input.type = 'file'
      input.multiple = 'multiple'
      input.click()
      input.addEventListener('change', () => {
        this.transfile.uploadingFiles.length = 0
        this.transfile.show = true
        this.transfile.uploadingFilesLen = input.files.length
        Array.from(input.files).forEach((file, index) => {
          this.$set(this.transfile.uploadingFiles, index, {
            name: file.name,
            process: 0,
            cancel: undefined
          })

          const formData = new FormData()
          formData.append('files', file)
          formData.append('type', this.type)
          formData.append('relativePath', this.relativePath)
          this.axios({
            method: 'POST',
            url: '/resource/file',
            headers: {
              'Content-Type': 'multipart/form-data'
            },
            data: formData,
            onUploadProgress: (progressEvent) => {
              this.$set(this.transfile.uploadingFiles[index], 'process', progressEvent.loaded / progressEvent.total * 100)
              if (progressEvent.loaded === progressEvent.total) {
                this.transfile.uploadingFilesLen -= 1
              }
            },
            cancelToken: new this.axios.CancelToken((cancel) => {
              this.transfile.uploadingFiles[index].cancel = cancel
            })
          }).then((response) => {
            if (response.data.status === 1) {
              this.$store.commit('openSnackbar', {
                type: 'done',
                text: response.data.message
              })
              this.getFiles()
            }
          })
        })
      })
    },
    cancelUpload () {
      this.transfile.show = false
      Array.from(this.transfile.uploadingFiles).forEach((item, index) => {
        if (item.process !== 100) {
          item.cancel('File upload canceled')
        }
      })
      this.transfile.uploadingFiles.length = 0
    },
    handleDblclick (item) {
      if (item.isDirectory) {
        this.relativePathArr.push(item.name)
        this.getFiles()
      }
    },
    back () {
      this.relativePathArr.pop()
      this.getFiles()
    },
    showMenu (event, file) {
      event.preventDefault()
      this.operatingFile = file
      this.menu.show = false
      this.menu.x = event.clientX
      this.menu.y = event.clientY
      this.$nextTick(() => {
        this.menu.show = true
      })
    },
    download () {
      this.axios({
        method: 'GET',
        url: '/resource/download',
        params: {
          type: this.type,
          name: this.operatingFile.name,
          relativePath: this.relativePath
        }
      }).then((response) => {
        const blob = new Blob([response.data], { type: 'application/octet-stream' })
        const a = document.createElement('a')
        a.download = this.operatingFile.name
        a.href = URL.createObjectURL(blob)
        a.click()
      })
    },
    renameFile () {
      this.renameDialog.show = false
      this.axios({
        method: 'PUT',
        url: '/resource',
        data: {
          type: this.type,
          oldName: this.operatingFile.name,
          newName: this.renameDialog.name,
          relativePath: this.relativePath
        }
      }).then((response) => {
        if (response.data.status === 1) {
          this.$store.commit('openSnackbar', {
            type: 'done',
            text: response.data.message
          })
          this.getFiles()
        }
        this.renameDialog.name = ''
      })
    },
    removeFile () {
      this.deleteDialog.show = false
      this.axios({
        method: 'DELETE',
        url: '/resource',
        data: {
          type: this.type,
          name: this.operatingFile.name,
          relativePath: this.relativePath
        }
      }).then((response) => {
        if (response.data.status === 1) {
          this.$store.commit('openSnackbar', {
            type: 'done',
            text: response.data.message
          })
          this.getFiles()
        }
      })
    }
  }
}
</script>

<style scoped>
.operation-button {
  margin-right: 10px;
  margin-bottom: 10px;
}

.file {
  user-select: none;
  cursor: pointer;
}

.file svg {
  width: 100px;
  height: 100px;
  margin-bottom: 20px;
}

.file span {
  width: 100px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  text-align: center;
}
</style>
