<template>
  <v-row>
    <v-col
      cols="12"
      md="6"
    >
      <v-data-table
        :headers="headers"
        :items="desserts"
        :items-per-page="5"
        class="elevation-1"
      />
    </v-col>

    <v-col
      cols="12"
      md="6"
    >
      <v-card>
        <v-card-title>添加新合同</v-card-title>
        <v-card-text>
          <v-form>
            <v-text-field label="合同名称" />

            <v-text-field label="项目名称" />

            <v-textarea label="发货地址" />

            <v-menu
              v-model="datePickerMenu"
              :close-on-content-click="false"
              :nudge-right="40"
              transition="scale-transition"
              offset-y
              min-width="auto"
            >
              <template v-slot:activator="{ on, attrs }">
                <v-text-field
                  v-model="form.date"
                  label="点击选择日期"
                  prepend-icon="mdi-calendar"
                  readonly
                  v-bind="attrs"
                  v-on="on"
                />
              </template>
              <v-date-picker
                v-model="form.date"
                @input="datePickerMenu = false"
              />
            </v-menu>

            <v-file-input
              label="添加合同附件"
              @change="uploadFile"
            />
          </v-form>
        </v-card-text>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
export default {
  name: 'Contract',
  data () {
    return {
      form: {
        date: undefined,
        file: undefined
      }
    }
  },
  methods: {
    uploadFile (file) {
      if (file) {
        const formData = new FormData()
        formData.append('file', file)
        this.axios({
          method: 'POST',
          url: '/crm/customer/contract/file',
          data: formData
        }).then((response) => {
          if (response.data.status === 1) {
            this.form.file = response.data.data.fileName
          }
        })
      }
    }
  }
}
</script>

