<template>
  <v-row>
    <v-col>
      <v-card>
        <v-card-title>报价</v-card-title>
        <v-card-text>
          <v-data-table
            :headers="offerHeaders"
            :items="offers"
          >
            <template v-slot:item.date="{ item }">
              {{ moment(item.date).format('YYYY-MM-DD HH:mm') }}
            </template>

            <template v-slot:item.actions="{ item }">
              <v-btn
                small
                class="mr-2"
                @click="editItem(item)"
              >
                查看
              </v-btn>
              <v-btn
                small
                @click="deleteItem(item)"
              >
                删除
              </v-btn>
            </template>
          </v-data-table>
        </v-card-text>
      </v-card>
    </v-col>

    <v-col>
      <v-card>
        <v-card-title>添加报价</v-card-title>
        <v-card-text>
          <v-text-field
            label="报价名称"
            v-model="form.name"
          />

          <v-card-subtitle>
            产品明细

            <v-dialog
              max-width="300"
              v-model="productDialog.show"
            >
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  icon
                  v-bind="attrs"
                  v-on="on"
                >
                  <v-icon>mdi-plus</v-icon>
                </v-btn>
              </template>

              <v-card>
                <v-card-title>添加新产品项</v-card-title>
                <v-card-text>
                  <v-form>
                    <v-text-field
                      label="产品名"
                      autofocus
                      v-model="productDialog.name"
                    />
                    <v-text-field
                      label="数量"
                      type="number"
                      v-model="productDialog.count"
                    />
                    <v-text-field
                      label="单价"
                      type="number"
                      v-model="productDialog.price"
                    />
                  </v-form>
                </v-card-text>

                <v-card-actions>
                  <v-spacer />
                  <v-btn @click="productDialog = false">
                    取消
                  </v-btn>
                  <v-btn @click="addProduct">
                    确定
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </v-card-subtitle>
          <v-data-table
            dense
            :headers="productsHeaders"
            :items="form.products"
            item-key="name"
            disable-pagination
            disable-sort
            hide-default-footer
          />

          <v-card-subtitle>
            服务

            <v-dialog
              max-width="300"
              v-model="serviceDialog.show"
            >
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  icon
                  v-bind="attrs"
                  v-on="on"
                >
                  <v-icon>mdi-plus</v-icon>
                </v-btn>
              </template>

              <v-card>
                <v-card-title>添加新服务项</v-card-title>
                <v-card-text>
                  <v-form>
                    <v-text-field
                      label="服务名"
                      autofocus
                      v-model="serviceDialog.name"
                    />
                    <v-text-field
                      label="价格"
                      type="number"
                      v-model="serviceDialog.price"
                    />
                  </v-form>
                </v-card-text>

                <v-card-actions>
                  <v-spacer />
                  <v-btn @click="serviceDialog = false">
                    取消
                  </v-btn>
                  <v-btn @click="addService">
                    确定
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </v-card-subtitle>
          <v-data-table
            dense
            :headers="serviceHeaders"
            :items="form.service"
            item-key="name"
            disable-pagination
            disable-sort
            hide-default-footer
          />

          <v-card-subtitle>物流</v-card-subtitle>
          <v-text-field
            label="物流名称"
            v-model="form.ship.name"
          />
          <v-text-field
            label="收货地址"
            v-model="form.ship.address"
          />
          <v-text-field
            label="价格"
            type="number"
            v-model="form.ship.price"
          />

          <v-card-subtitle>税</v-card-subtitle>
          <v-text-field
            label="税费"
            type="number"
            v-model="form.tax"
          />

          <v-card-subtitle>总价</v-card-subtitle>
          <v-text-field
            label="价格"
            type="number"
            v-model="form.total"
          />

          <v-card-actions>
            <v-spacer />
            <v-btn @click="addOffer">
              添加
            </v-btn>
          </v-card-actions>
        </v-card-text>
      </v-card>
    </v-col>

    <!--  删除框  -->
    <v-dialog
      v-model="deleteDialog"
      max-width="300"
    >
      <v-card>
        <v-card-title>删除报价</v-card-title>
        <v-card-text>确定要删除这个报价吗?</v-card-text>
        <v-card-actions>
          <v-spacer />
          <v-btn @click="deleteDialog = false">
            取消
          </v-btn>
          <v-btn @click="deleteItemConfirm">
            确定
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
import moment from 'moment'

export default {
  name: 'Offer',
  data () {
    return {
      moment,
      offerHeaders: [
        {
          text: '创建时间',
          value: 'date'
        },
        {
          text: '名称',
          value: 'name'
        },
        {
          text: '操作',
          value: 'actions',
          sortable: false
        }
      ],
      offers: [],
      operating: undefined,
      deleteDialog: false,
      productsHeaders: [
        {
          text: '名称',
          value: 'name'
        },
        {
          text: '数量',
          value: 'count'
        },
        {
          text: '单价',
          value: 'price'
        }
      ],
      productDialog: {
        show: false,
        name: undefined,
        price: undefined,
        count: undefined
      },
      serviceHeaders: [
        {
          text: '名称',
          value: 'name'
        },
        {
          text: '价格',
          value: 'price'
        }
      ],
      serviceDialog: {
        show: false,
        name: undefined,
        price: undefined
      },
      form: {
        name: undefined,
        products: [],
        service: [],
        ship: {
          name: undefined,
          address: undefined,
          price: undefined
        },
        tax: undefined,
        total: undefined
      }
    }
  },
  created () {
    this.getOffer()
  },
  methods: {
    download () {

    },
    deleteItem (item) {
      this.operating = item._id
      this.deleteDialog = true
    },
    deleteItemConfirm () {
      this.axios({
        method: 'DELETE',
        url: '/crm/customer/offer',
        data: {
          _id: this.operating
        }
      }).then((response) => {
        if (response.data.status === 1) {
          this.getOffer()
        }
      })
      this.deleteDialog = false
    },
    getOffer () {
      this.axios({
        method: 'GET',
        url: '/crm/customer/offer',
        params: {
          query: {},
          options: {}
        }
      }).then((response) => {
        if (response.data.status === 1) {
          this.offers = response.data.data.offers
        }
      })
    },
    addProduct () {
      this.form.products.push({
        name: this.productDialog.name,
        price: this.productDialog.price,
        count: this.productDialog.count
      })
      this.productDialog = {
        show: false,
        name: undefined,
        price: undefined,
        count: undefined
      }
    },
    addService () {
      this.form.service.push({
        name: this.serviceDialog.name,
        price: this.serviceDialog.price
      })
      this.serviceDialog = {
        show: false,
        name: undefined,
        price: undefined
      }
    },
    addOffer () {
      this.form.date = Date.now()
      this.axios({
        method: 'POST',
        url: '/crm/customer/offer',
        data: this.form
      }).then((response) => {
        if (response.data.status === 1) {
          this.getOffer()
          this.form = {
            name: undefined,
            products: [],
            service: [],
            ship: {
              name: undefined,
              address: undefined,
              price: undefined
            },
            tax: undefined,
            total: undefined
          }
        }
      })
    }
  }
}
</script>

