<template>
  <v-app>
    <v-container class="d-flex justify-center">
      <v-card class="form">
        <v-img
          class="mt-5 mx-auto"
          src="https://www.hechuangzhiyun.com/favicon.ico"
          width="50"
          height="50"
        />

        <v-card-title class="justify-center">
          管理系统登录
        </v-card-title>

        <!--  In Chrome : Autofocus processing was blocked because a document already has a focused element. -->
        <v-card-text>
          <v-form ref="form">
            <v-text-field
              id="username"
              autofocus
              clearable
              counter="20"
              label="用户名"
              management-system-login-
              :rules="[rules.required, rules.counter, rules.character]"
              v-model="username"
            />
            <v-text-field
              id="password"
              clearable
              counter="20"
              label="密码"
              :rules="[rules.required, rules.counter, rules.character]"
              type="password"
              v-model="password"
            />
          </v-form>
        </v-card-text>

        <v-card-actions class="mb-4">
          <v-spacer />
          <v-btn
            id="button"
            @click="submit"
          >
            提交
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-container>
  </v-app>
</template>

<script>
import CryptoJS from 'crypto-js'
import FingerprintJS from '@fingerprintjs/fingerprintjs'

export default {
  data () {
    return {
      username: '',
      password: '',
      rules: {
        required: value => !!value || '必填',
        counter: value => (value.length >= 4 && value.length <= 20) || '4-20个字符',
        character: value => /^[\x21-\x7E]/.test(value) || '请输入有效的字符'
      },
      fingerprint: undefined
    }
  },
  created () {
    // 获取用户的指纹
    (async () => {
      const fp = await FingerprintJS.load()
      const result = await fp.get()
      this.fingerprint = result.visitorId
    })()
  },
  methods: {
    submit () {
      // 验证表单填写是否合格
      if (this.$refs.form.validate()) {
        this.axios({
          method: 'POST',
          url: '/passport/authentication',
          data: {
            username: this.username,
            password: CryptoJS.MD5(this.password).toString(),
            fingerprint: this.fingerprint
          }
        }).then((response) => {
          if (response.data.status === 1) {
            localStorage.setItem('token', response.data.data.token)
            localStorage.setItem('exp', Date.now() + 3500000)
            this.$router.push('/index')
          }
        })
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.v-application {
  background-image: url("../assets/background.png");
}

.form {
  @media (max-width: 359px) {
    width: 95%;
    margin-top: 100px;
  }

  @media (min-width: 360px) {
    width: 350px;
    margin-top: 150px;
  }

  @media (min-width: 600px) {
    margin-top: 200px;
  }
}
</style>
