<template>
  <v-card>
    <!--  顶部导航栏：搜索框，添加新客户按钮  -->
    <v-card-title>
      <!--  搜索框    -->
      <v-text-field
        clearable
        class="mr-4"
        v-model="search"
        append-icon="mdi-magnify"
        label="要搜索的内容"
        single-line
        hide-details
      />

      <v-select
        dense
        hide-details
        v-model="selectedBelong"
        :items="belongs"
        item-text="name"
        item-value="value"
        label="筛选归属人"
        outlined
        @change="search = selectedBelong"
      />
      <v-spacer />

      <!--  添加新客户页面  -->
      <v-dialog
        fullscreen
        v-model="addDialog"
      >
        <template v-slot:activator="{ on, attrs }">
          <v-btn
            class="mt-4 mt-sm-0"
            v-bind="attrs"
            v-on="on"
            @click="status = 'add'"
          >
            添加新客户
          </v-btn>
        </template>

        <v-card>
          <v-card-title class="mb-5">
            添加新客户
          </v-card-title>

          <v-card-text>
            <v-row>
              <v-col
                cols="12"
                sm="6"
              >
                <v-card-subtitle>客户信息</v-card-subtitle>

                <div>
                  <v-text-field
                    clearable
                    label="客户名"
                    autofocus
                    v-model="form.name"
                  />

                  <v-btn
                    class="mr-4"
                    @click="queryInformation"
                  >
                    查信息
                  </v-btn>
                  <v-btn @click="checkDuplicate">
                    查重
                  </v-btn>
                </div>

                <!--  级联选择器，暂时使用element的来做  -->
                <el-cascader
                  id="selectArea"
                  v-model="form.area"
                  :options="areas"
                  size="medium"
                  :props="{value:'name',label:'name' }"
                  placeholder="请选择客户所在的省市区"
                  style="width: 100%;padding: 14px 0"
                />

                <v-text-field
                  clearable
                  label="详细地址"
                  v-model="form.address"
                />

                <v-select
                  clearable
                  :items="$store.state.field.fields.source"
                  label="客户来源"
                  v-model="form.source"
                />

                <v-select
                  clearable
                  :items="$store.state.field.fields.type"
                  label="客户类型"
                  v-model="form.type"
                />

                <v-select
                  clearable
                  :items="$store.state.field.fields.equipment"
                  label="咨询设备"
                  v-model="form.equipment"
                />

                <!--                <v-select-->
                <!--                  :items="$store.state.field.fields.industry"-->
                <!--                  label="所属行业"-->
                <!--                  v-model="form.industry"-->
                <!--                />-->

                <v-textarea
                  clearable
                  label="备注"
                  v-model="form.remark"
                />
              </v-col>

              <v-col
                cols="12"
                sm="6"
              >
                <div class="d-flex">
                  <v-card-subtitle>联系人</v-card-subtitle>

                  <!--       添加联系人           -->
                  <v-dialog
                    max-width="300"
                    v-model="addContactDialog"
                  >
                    <template v-slot:activator="{ on, attrs }">
                      <v-icon
                        v-bind="attrs"
                        v-on="on"
                      >
                        mdi-plus
                      </v-icon>
                    </template>

                    <v-card>
                      <v-card-title>添加联系人</v-card-title>
                      <v-card-text>
                        <v-text-field
                          label="姓名"
                          autofocus
                          v-model="contact.name"
                        />
                        <v-text-field
                          label="职位"
                          v-model="contact.position"
                        />
                        <v-text-field
                          label="手机号"
                          v-model="contact.phone"
                        />
                        <v-text-field
                          label="微信"
                          v-model="contact.wechat"
                        />
                      </v-card-text>
                      <v-card-actions>
                        <v-spacer />
                        <v-btn @click="addContactDialog = false">
                          取消
                        </v-btn>
                        <v-btn
                          @click="addContact"
                        >
                          添加
                        </v-btn>
                      </v-card-actions>
                    </v-card>
                  </v-dialog>
                </div>

                <v-data-table
                  :headers="contactHeaders"
                  :items="form.contacts"
                  hide-default-footer
                >
                  <template v-slot:item.actions="{ item }">
                    <v-icon
                      small
                      @click="deleteContact(item)"
                    >
                      mdi-delete
                    </v-icon>
                  </template>

                  <template v-slot:no-data>
                    点击加号添加联系人
                  </template>
                </v-data-table>

                <v-card-subtitle>客户归属</v-card-subtitle>

                <v-select
                  clearable
                  :items="$store.state.field.fields.level"
                  label="客户级别"
                  v-model="form.level"
                />

                <v-select
                  v-if="status === 'add'"
                  :items="belong"
                  label="客户归属"
                  v-model="form.belong"
                />
              </v-col>
            </v-row>
          </v-card-text>

          <v-card-actions>
            <v-spacer />
            <v-btn @click="addDialog = false">
              取消
            </v-btn>
            <v-btn
              v-if="status === 'add'"
              @click="addCustomer"
            >
              添加
            </v-btn>

            <v-btn
              v-if="status === 'edit'"
              @click="submitEdit"
            >
              修改
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-card-title>

    <!--  查重结果弹窗  -->
    <v-dialog
      v-model="checkDuplicateDialog"
      max-width="300"
    >
      <v-card>
        <v-card-title>查重结果</v-card-title>
        <v-card-text>
          <v-list>
            <v-list-item
              v-for="(duplicate,index) in duplicates"
              :key="index"
            >
              {{ duplicate.name }}
              相似度：{{ duplicate.score }}
            </v-list-item>
          </v-list>
        </v-card-text>
        <v-card-actions>
          <v-spacer />
          <v-btn>关闭</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- 客户列表   -->
    <v-data-table
      :headers="headers"
      :items="items"
      :search="search"
    >
      <template
        v-if="administratorDir"
        v-slot:item.belong="{ item }"
      >
        <span v-if="item.belong.indexOf('openSea') !== -1">公海</span>
        <div v-else>
          <span
            v-for="(admin,index) in item.belong"
            :key="index"
          >
            {{ administratorDir[admin].name }}
          </span>
        </div>
      </template>

      <template v-slot:item.date="{ item }">
        {{ moment(item.date).format('YYYY-MM-DD HH:mm:ss') }}
      </template>

      <template v-slot:item.actions="{ item }">
        <v-icon
          small
          @click="$store.state.operating.operating = item;detailDialog = true"
          title="操作"
        >
          mdi-details
        </v-icon>

        <v-icon
          small
          @click="editCustomer(item)"
          title="编辑"
        >
          mdi-pencil
        </v-icon>

        <v-icon
          small
          title="转移所有权"
          @click="operating = item;transferOwnershipDialog = true"
        >
          mdi-forward
        </v-icon>

        <v-icon
          small
          title="日志"
          @click="openLogDialog(item)"
        >
          mdi-notebook
        </v-icon>

        <!--        <v-icon-->
        <!--          small-->
        <!--          @click="operating = item;deleteDialog = true"-->
        <!--          title="删除客户"-->
        <!--        >-->
        <!--          mdi-delete-->
        <!--        </v-icon>-->
      </template>
    </v-data-table>

    <!--  删除客户弹窗  -->
    <v-dialog
      v-model="deleteDialog"
      max-width="300"
    >
      <v-card>
        <v-card-title>删除客户</v-card-title>
        <v-card-text>
          确定要删除这个客户吗？
        </v-card-text>
        <v-card-actions>
          <v-spacer />
          <v-btn @click="deleteDialog = false">
            取消
          </v-btn>
          <v-btn @click="deleteCustomer">
            确定
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!--  详情框  -->
    <v-dialog
      fullscreen
      v-model="detailDialog"
    >
      <v-card>
        <v-toolbar>
          <v-tabs
            fixed-tabs
            background-color="indigo"
            dark
            v-model="currentTab"
          >
            <v-tab
              v-for="(tab,index) in tabs"
              :key="index"
            >
              {{ tab.name }}
            </v-tab>
          </v-tabs>

          <v-btn
            icon
            color="indigo"
            @click="detailDialog = false"
          >
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-toolbar>

        <v-tabs-items v-model="currentTab">
          <v-tab-item
            v-for="(tab,index) in tabs"
            :key="index"
          >
            <component :is="tab.value" />
          </v-tab-item>
        </v-tabs-items>
      </v-card>
    </v-dialog>

    <!--  转移所有权弹窗  -->
    <v-dialog
      v-model="transferOwnershipDialog"
      max-width="300"
    >
      <v-card>
        <v-card-title>转移所有权</v-card-title>
        <v-card-text>
          <v-select
            v-model="transferOwnershipType"
            :items="selectItems"
            item-text="text"
            item-value="value"
            label="选择转移类型"
            outlined
          />
          <v-text-field
            autofocus
            outlined
            label="搜索要转移的对象"
            v-model="searchInput"
            @compositionend="searchAdministrator"
          />
          <v-radio-group
            v-if="selectAdministrator.length !== 0"
            v-model="selectedAdministrator"
          >
            <v-radio
              v-for="administrator in selectAdministrator"
              :key="administrator._id"
              :label="administrator.name"
              :value="administrator._id"
            />
          </v-radio-group>

          <span v-else>没有搜索结果</span>
        </v-card-text>
        <v-card-actions>
          <v-spacer />
          <v-btn @click="transferOwnershipDialog = false">
            取消
          </v-btn>
          <v-btn @click="transferOwnership">
            确定
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!--  日志  -->
    <v-dialog v-model="logDialog">
      <v-card>
        <v-card-title>日志</v-card-title>
        <v-card-text>
          <v-list>
            <v-list-item
              two-line
              v-for="(log,index) in logs"
              :key="index"
            >
              <v-list-item-content>
                <v-list-item-title>{{ moment(log.date).format('YYYY-MM-DD HH:mm:ss') }}</v-list-item-title>
                <v-list-item-subtitle>
                  {{ administratorDir[log.administrator].name }} {{
                    log.content
                  }}
                </v-list-item-subtitle>
              </v-list-item-content>
            </v-list-item>
          </v-list>
        </v-card-text>
        <v-card-actions>
          <v-spacer />
          <v-btn @click="logDialog = false">
            关闭
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-card>
</template>

<script>
import AfterSale from '@/components/crm/AfterSale'
import Contract from '@/components/crm/Contract'
import Offer from '@/components/crm/Offer'
import Scheduling from '@/components/crm/Scheduling'
import Ship from '@/components/crm/Ship'
import Visit from '@/components/crm/Visit'
import moment from 'moment'

export default {
  name: 'Client',
  components: {
    afterSale: AfterSale,
    contract: Contract,
    offer: Offer,
    scheduling: Scheduling,
    ship: Ship,
    visit: Visit
  },
  data () {
    return {
      moment,
      status: '',
      administratorDir: null, // 管理员字典，通过id查到管理员信息
      search: '',
      headers: [
        {
          text: '创建时间',
          value: 'date',
          sortable: false
        },
        {
          text: '客户名',
          value: 'name',
          sortable: false
        },
        {
          text: '地域',
          value: 'area',
          sortable: false
        },
        {
          text: '客户来源',
          value: 'source',
          sortable: false
        },
        {
          text: '类型',
          value: 'type'
        },
        {
          text: '所属行业',
          value: 'industry'
        },
        {
          text: '客户级别',
          value: 'level'
        },
        {
          text: '归属',
          value: 'belong'
        },
        {
          text: '操作',
          value: 'actions',
          sortable: false
        }
      ],
      items: [],
      checkDuplicateDialog: false,
      duplicates: [],
      addDialog: false,
      addContactDialog: false,
      areas: [],
      belong: [
        {
          text: '个人',
          value: 'personal'
        },
        {
          text: '公海',
          value: 'openSea'
        }
      ],
      contactHeaders: [
        {
          text: '姓名',
          value: 'name',
          sortable: false
        },
        {
          text: '职位',
          value: 'position',
          sortable: false
        },
        {
          text: '手机号',
          value: 'phone',
          sortable: false
        },
        {
          text: '微信号',
          value: 'wechat',
          sortable: false
        },
        {
          text: '操作',
          value: 'actions',
          sortable: false
        }
      ],
      form: {
        name: '',
        area: '',
        address: '',
        industry: '',
        type: '',
        equipment: '',
        remark: '',
        contacts: [],
        belong: 'personal'
      },
      contact: {
        name: '',
        position: '',
        phone: '',
        wechat: ''
      },
      deleteDialog: false,
      operating: '',
      detailDialog: false,
      currentTab: null,
      tabs: [
        {
          name: '拜访',
          value: 'visit'
        },
        {
          name: '报价',
          value: 'offer'
        },
        {
          name: '合同',
          value: 'contract'
        },
        {
          name: '排产',
          value: 'scheduling'
        },
        {
          name: '发货',
          value: 'ship'
        },
        {
          name: '售后',
          value: 'afterSale'
        }
      ],
      transferOwnershipDialog: false,
      selectItems: [
        {
          text: '所有权转移',
          value: 0
        },
        {
          text: '协作',
          value: 1
        },
        {
          text: '转移到公海',
          value: 2
        }
      ],
      transferOwnershipType: 0,
      searchInput: '',
      selectAdministrator: [],
      selectedAdministrator: '',
      logDialog: false,
      logs: [],
      belongs: [],
      selectedBelong: ''
    }
  },
  created () {
    const that = this
    ;(async function () {
      // 获取所有管理员信息并生成字典
      await that.getAdministrator()

      // 获取客户
      await that.getCustomer()
    }())

    // 获取地域
    this.axios({
      method: 'GET',
      url: '/map',
      params: {}
    }).then((response) => {
      if (response.data.status === 1) {
        this.areas = response.data.data.area
      }
    })

    // 获取字段
    this.$store.commit('getItems', 'source')
    this.$store.commit('getItems', 'type')
    this.$store.commit('getItems', 'industry')
    this.$store.commit('getItems', 'level')
    this.$store.commit('getItems', 'equipment')
  },
  methods: {
    getAdministrator () {
      this.axios({
        methods: 'GET',
        url: '/administrator',
        params: {
          query: {},
          options: {}
        }
      }).then((response) => {
        if (response.data.status === 1) {
          // 生成管理员字典
          const administratorDir = {}
          response.data.data.administrator.forEach((administrator) => {
            administratorDir[administrator._id] = administrator
          })
          this.administratorDir = administratorDir
        }
      })
    },
    queryInformation () {
      window.open(encodeURI(`https://aiqicha.baidu.com/s?q=${this.form.name}`))
    },
    checkDuplicate () {
      this.checkDuplicateDialog = true
      this.axios({
        method: 'GET',
        url: '/crm/customer/check-duplicate',
        params: {
          name: this.form.name
        }
      }).then((response) => {
        console.log(response.data)
        if (response.data.status === 1) {
          this.duplicates = response.data.data.duplicates
        }
      })
    },
    deleteContact (item) {
      const index = this.form.contacts.findIndex((contact) => {
        return contact.name === item.name
      })
      this.form.contacts.splice(index, 1)
    },
    addContact () {
      this.form.contacts.push(JSON.parse(JSON.stringify(this.contact)))
      this.addContactDialog = false
      this.contact = {
        name: '',
        position: '',
        phone: '',
        wechat: ''
      }
    },
    getCustomer () {
      this.axios({
        method: 'GET',
        url: '/crm/customer'
      }).then((response) => {
        if (response.data.status === 1) {
          const belongs = new Set()
          response.data.data.customer.forEach((customer) => {
            customer.belong.forEach((item) => {
              belongs.add({
                name: this.administratorDir[item].name,
                value: item
              })
            })
          })
          this.belongs = Array.from(belongs)
          this.items = response.data.data.customer
        }
      })
    },
    addCustomer () {
      delete this.form._id
      this.form.date = Date.now()
      this.axios({
        method: 'POST',
        url: '/crm/customer',
        data: {
          customer: this.form
        }
      }).then((response) => {
        if (response.data.status === 1) {
          this.getCustomer()
        }
        this.addDialog = false
      })
    },
    submitEdit () {
      this.axios({
        method: 'PUT',
        url: '/crm/customer',
        data: {
          query: {
            _id: this.form._id
          },
          doc: this.form
        }
      }).then((response) => {
        if (response.data.status === 1) {
          this.getCustomer()
        }
        this.addDialog = false
      })
    },
    editCustomer (item) {
      this.operating = item._id
      this.status = 'edit'
      this.form = item
      this.addDialog = true
    },
    deleteCustomer () {
      this.axios({
        method: 'DELETE',
        url: '/crm/customer',
        data: {
          _id: this.operating._id
        }
      }).then((response) => {
        if (response.data.status === 1) {
          this.getCustomer()
          this.deleteDialog = false
        }
      })
    },
    searchAdministrator () {
      this.axios({
        method: 'GET',
        url: '/administrator',
        params: {
          query: { name: { $regex: this.searchInput } },
          options: {}
        }
      }).then((response) => {
        if (response.data.status === 1) {
          this.selectAdministrator = response.data.data.administrator
        }
      })
    },
    transferOwnership () {
      if ((this.transferOwnershipType < 2 && this.selectedAdministrator) || this.transferOwnershipType === 2) {
        let newBelong = []
        if (this.transferOwnershipType === 0) {
          newBelong.push(this.selectedAdministrator)
        } else if (this.transferOwnershipType === 1) {
          newBelong = this.operating.belong.concat([this.selectedAdministrator])
        } else if (this.transferOwnershipType === 2) {
          newBelong = ['openSea']
        }
        this.axios({
          method: 'PUT',
          url: '/crm/customer/transfer-ownership',
          data: {
            _id: this.operating._id,
            belong: newBelong
          }
        }).then((response) => {
          if (response.data.status === 1) {
            this.getCustomer()
            this.transferOwnershipDialog = false
          }
        })
      }
    },
    openLogDialog (item) {
      this.axios({
        method: 'GET',
        url: '/log',
        params: {
          operate: item._id
        }
      }).then((response) => {
        if (response.data.status === 1) {
          this.logs = response.data.data.logs
          this.logDialog = true
        }
      })
    }
  }
}
</script>

<style scoped>

</style>
