<template>
  <v-dialog
    v-model="moveDialog.show"
    persistent
    max-width="300"
  >
    <v-card>
      <v-card-title>移动部门</v-card-title>
      <v-card-text>
        选择要移动到那个部门下
        <v-radio-group v-model="moveDialog.selected">
          <v-radio
            v-for="(item,index) in moveDialog.items"
            :key="item._id"
            :label="item.name"
            :value="index"
          />
        </v-radio-group>
      </v-card-text>
      <v-card-actions>
        <v-spacer />
        <v-btn @click="$parent.moveDialog.show = false">
          cancel
        </v-btn>
        <v-btn @click="$emit('moveDepartment')">
          ok
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  props: ['moveDialog']
}
</script>

