<template>
  <v-dialog
    v-model="show"
    max-width="300"
  >
    <v-card>
      <v-card-title>修改密码</v-card-title>
      <v-card-text>
        <v-form ref="form">
          <v-text-field
            label="请输入新密码"
            type="password"
            autofocus
            v-model="newPassword"
            :rules="[rules.required, rules.counter, rules.character]"
          />
          <v-text-field
            label="请重复输入新密码"
            type="password"
            v-model="repeatNewPassword"
            :rules="[rules.required, rules.counter, rules.character]"
          />
        </v-form>
      </v-card-text>
      <v-card-actions>
        <v-spacer />
        <v-btn>取消</v-btn>
        <v-btn @click="changePassword">
          确定
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import CryptoJS from 'crypto-js'

export default {
  name: 'ResetPasswordDialog',
  props: {
    show: Boolean
  },
  data () {
    return {
      newPassword: '',
      repeatNewPassword: '',
      rules: {
        required: value => !!value || '必填',
        counter: value => (value.length >= 8 && value.length <= 20) || '不能少于8个字符，最多20个字符',
        character: value => /^[\x21-\x7E]/.test(value) || '请输入有效的字符'
      }
    }
  },
  methods: {
    changePassword () {
      if (this.newPassword !== this.repeatNewPassword) {
        this.$store.commit('openSnackbar', {
          type: 'error',
          text: '两次输入的密码不一致'
        })
      } else if (this.$refs.form.validate()) {
        this.axios({
          method: 'PUT',
          url: '/passport/password',
          data: {
            password: CryptoJS.MD5(this.newPassword).toString()
          }
        }).then((response) => {
          if (response.data.status === 1) {
            this.$store.commit('openSnackbar', {
              type: 'done',
              text: '修改密码成功'
            })
            localStorage.removeItem('token')
            localStorage.removeItem('exp')
            this.$router.push('/passport')
          }
        })
      }
    }
  }
}
</script>

