<template>
  <v-dialog
    v-model="show"
    persistent
    max-width="290"
  >
    <v-card>
      <v-card-title>Confirm deletion</v-card-title>
      <v-card-text>Are you sure to delete the file?</v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn
          color="green darken-1"
          text
          @click="$parent.deleteDialog.show = false"
        >
          cancel
        </v-btn>
        <v-btn
          color="green darken-1"
          text
          @click="$emit('removeFile')"
        >
          determine
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  props: {
    show: Boolean
  }
}
</script>

