export default {
  state: {
    fields: {
      source: [],
      type: [],
      industry: [],
      level: [],
      equipment: []
    }
  },
  mutations: {
    getItems (state, fieldName) {
      this._vm.axios({
        method: 'GET',
        url: `/settings/field/${fieldName}`
      }).then((response) => {
        if (response.data.status === 1) {
          state.fields[fieldName] = response.data.data.items
        }
      })
    }
  },
  actions: {},
  modules: {}
}
