<template>
  <v-dialog
    v-model="transfile.show"
    persistent
    max-width="300"
  >
    <v-card>
      <v-card-title class="headline">
        Uploading files
      </v-card-title>

      <v-card-text>
        <v-list>
          <v-list-item
            v-for="(item,index) in transfile.uploadingFiles"
            :key="index"
          >
            <v-list-item-title>{{ item.name }}</v-list-item-title>
            <v-progress-linear
              :value="item.process"
              :color="item.process === 100 ? 'success' : 'primary'"
            ></v-progress-linear>
          </v-list-item>
        </v-list>
      </v-card-text>

      <v-card-actions>
        <v-spacer/>
        <v-btn
          v-if="transfile.uploadingFilesLen !== 0"
          color="red darken-1"
          text
          @click="$emit('cancelUpload')"
        >
          cancel
        </v-btn>

        <v-btn
          v-else
          color="green darken-1"
          text
          @click="transfile.show = false"
        >
          done
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  props: ['transfile']
}
</script>

