<template>
  <v-menu
    v-model="menu.show"
    :position-x="menu.x"
    :position-y="menu.y"
    absolute
    offset-y
  >
    <v-list>
      <v-list-item @click="$emit('download')">
        <v-list-item-title>download</v-list-item-title>
      </v-list-item>

      <v-list-item @click="$emit('rename')">
        <v-list-item-title>rename</v-list-item-title>
      </v-list-item>

      <v-list-item @click="$emit('remove')">
        <v-list-item-title>delete</v-list-item-title>
      </v-list-item>
    </v-list>
  </v-menu>
</template>

<script>
export default {
  props: {
    menu: {
      type: Object,
      default: () => {
        return {
          show: false,
          x: 0,
          y: 0,
          items: [
            { title: 'download' },
            { title: 'rename' },
            { title: 'delete' }
          ]
        }
      }
    }
  }
}
</script>

