<template>
  <v-snackbar v-model="$store.state.snackbar.show">
    <v-icon class="mr-2">
      {{ $store.state.snackbar.type === 'done' ? 'mdi-check' : 'mdi-alert-circle-outline' }}
    </v-icon>
    <span>{{ $store.state.snackbar.text }}</span>
    <template v-slot:action="{ attrs }">
      <v-btn
        color="red"
        text
        v-bind="attrs"
        @click="$store.commit('closeSnackbar')"
      >
        Close
      </v-btn>
    </template>
  </v-snackbar>
</template>

<script>
export default {
  name: 'snackbar'
}
</script>

<style scoped>

</style>
